import "../../styles/AdminDash.css";
import "../../styles/CreateProduct.css";
import React, { useEffect, useState } from "react";
import AdminMenu from "../../components/AdminMenu";
import axios from "axios";
import toast from "react-hot-toast";
import { Select } from "antd";
import { useNavigate } from "react-router-dom";

const { Option } = Select;

const CreateProduct = () => {
  const navigate = useNavigate();
  const [categories, setCategories] = useState([]);
  const [category, setCategory] = useState("");
  const [name, setName] = useState("");
  const [description, setDescription] = useState("");
  const [price, setPrice] = useState("");
  const [isFeatured, setIsFeatured] = useState("");
  const [quantity, setQuantity] = useState("");
  const [photo, setPhoto] = useState("");

  // get all category
  const getAllCategory = async () => {
    try {
      const { data } = await axios.get(
        `/api/v1/category/allcategory`
      );
      if (data?.success) {
        setCategories(data?.category);
      }
    } catch (error) {
      toast.error("Something went wrong getting category");
    }
  };

  useEffect(() => {
    getAllCategory();
  }, []);

  // create product
  const handleCreate = async (e) => {
    e.preventDefault();
    try {
      const productData = new FormData();
      productData.append("name", name);
      productData.append("description", description);
      productData.append("category", category);
      productData.append("price", price);
      productData.append("isFeatured", isFeatured);
      productData.append("quantity", quantity);
      productData.append("photo", photo);

      const { data } = await axios.post(
        `/api/v1/product/create-product`,
        productData
      );
      if(data?.success){
        toast.success("Product created successfully")
        navigate("/profile/admin/products")
      }else{
        toast.error("Product creation failed")
      }

    } catch (error) {
      toast.error("Something went wrong creating product");
    }
  };

  return (
    <div className="admin_page">
      <div className="admin_menu">
        <AdminMenu />
      </div>
      <div className="admin_prod_right">
        <h1> Create Product </h1>
        <div className="admin_prod_cont">
          <Select
            placeholder="Select Category"
            size="large"
            showSearch
            onChange={(value) => {
              setCategory(value);
            }}
            className="prod-form-input"
          >
            {categories?.map((c) => (
              <Option key={c._id} value={c._id}>
                {c.name}
              </Option>
            ))}
          </Select>
          <div className="photo-btn">
              <label>
                {photo ? photo.name : "Select Product Image"}
                <input
                  type="file"
                  name="photo"
                  accept="image/*"
                  onChange={(e) => setPhoto(e.target.files[0])}
                  hidden
                />
              </label>
          </div>
          <div className="prod-form">
            <input
              type="text"
              value={name}
              placeholder="Product Name"
              className="prod-form-input"
              onChange={(e) => setName(e.target.value)}
            />
          </div>
          <div className="prod-form">
            <input
              type="text"
              value={description}
              placeholder="Product Description"
              className="prod-form-input"
              onChange={(e) => setDescription(e.target.value)}
            />
          </div>
          <div className="prod-form">
            <input
              type="number"
              value={price}
              placeholder="Product Price"
              className="prod-form-input"
              onChange={(e) => setPrice(e.target.value)}
            />
          </div>
          <div className="prod-form">
            <Select
              variant="false"
              placeholder="Select Featured"
              size="large"
              showSearch
              onChange={(value) => {
                setIsFeatured(value);
              }}
              className="prod-form-select"
            >
              <Option value="0"> No </Option>
              <Option value="1"> Yes </Option>
            </Select>
          </div>
          <div className="prod-form">
            <input
              type="number"
              value={quantity}
              placeholder="Quantity"
              className="prod-form-input"
              onChange={(e) => setQuantity(e.target.value)}
            />
          </div>

          <button className="prod-form-btn" onClick={handleCreate}>
            create porduct
          </button>
        </div>
      </div>
    </div>
  );
};

export default CreateProduct;
